// app/credit-customers/page.tsx
"use client";

import React, { useEffect, useState } from "react";
import { collection, getDocs } from "firebase/firestore";
import { db } from "../../lib/"; // sizning firebase.js faylingizdagi export qilingan db

interface Customer {
  id: string;
  name: string;
  productName: string;
  productCost: number;
  monthlyPayment: number;
  nextPaymentDate: string;
  history: string[];
  status: "Paid" | "Pending" | "Overdue";
}

const CreditCustomersPage: React.FC = () => {
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCustomers = async () => {
      const querySnapshot = await getDocs(collection(db, "creditCustomers"));
      const customersData: Customer[] = [];
      querySnapshot.forEach((doc) => {
        customersData.push({ id: doc.id, ...doc.data() } as Customer);
      });
      setCustomers(customersData);
      setLoading(false);
    };

    fetchCustomers();
  }, []);

  if (loading) return <p>Ma'lumot yuklanmoqda...</p>;

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Credit Customers</h1>
      <table className="w-full border border-gray-300">
        <thead>
          <tr className="bg-gray-200">
            <th className="border px-4 py-2">Customer's Name</th>
            <th className="border px-4 py-2">Product Name</th>
            <th className="border px-4 py-2">Product Cost</th>
            <th className="border px-4 py-2">Monthly Payment</th>
            <th className="border px-4 py-2">Next Payment Date</th>
            <th className="border px-4 py-2">History</th>
            <th className="border px-4 py-2">Status</th>
          </tr>
        </thead>
        <tbody>
          {customers.map((customer) => (
            <tr key={customer.id} className="text-center">
              <td className="border px-4 py-2">{customer.name}</td>
              <td className="border px-4 py-2">{customer.productName}</td>
              <td className="border px-4 py-2">{customer.productCost.toLocaleString()} UZS</td>
              <td className="border px-4 py-2">{customer.monthlyPayment.toLocaleString()} UZS</td>
              <td className="border px-4 py-2">{customer.nextPaymentDate}</td>
              <td className="border px-4 py-2">
                <button
                  className="bg-blue-500 text-white px-2 py-1 rounded"
                  onClick={() => alert(customer.history.join("\n"))}
                >
                  Koâ€˜rish
                </button>
              </td>
              <td className="border px-4 py-2">{customer.status}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CreditCustomersPage;